.todoItem{
    border: 1px solid #eee;
    display: flex;
    line-height: 3;
    padding: 0 30px;
    border-radius: 30px;
    margin-top: 10px;
    cursor:pointer;
    background-color: rgb(140, 183, 233);
}
.remove{
    margin-right:20px;
    color: rgb(13, 6, 100);
    font-weight: bold;
    opacity: 0;
}
.todoItem:hover .remove{
    opacity:1;
}
.checked{
    text-decoration: line-through;
    color:#ccc;
}
