.skillWrap section>div{
    display: flex;
    /* justify-content: space-between; */
    flex-wrap: wrap;
}
.skillWrap section>div .box{
    display: block;
    text-align: center;
    margin:10px 2% 10px 0;
    width:23%;
}
.skillWrap section>div .imgBox{
    /* max-width: 120px; */
    width: 100%;
    box-sizing: border-box;
}
.skillWrap .backWrap>.box:first-child img{
    max-width: 60px;
    width: 100%;
}
.skillWrap img{
    max-width: 80px;
    width:100%;
}
.skillWrap>section{
    padding-bottom: 5%;
}
.skillWrap>section>h2{
    padding-bottom: 3%;
}