body {
  color: white;
  background-color: #47ddd1;
  cursor: url('/public/images/cursor_green.cur'), auto;
}

* {
  margin: 0;
  padding: 0;
}

li {
  list-style: none;
}

a {
  text-decoration: none;
}

img {
  vertical-align: middle;
}

.innerBox {
  max-width: 1200px;
  margin: 0 auto;
  position: absolute;
}

header {
  border-bottom: 1px solid #333;
}

header>.innerBox {
  display: flex;
  justify-content: space-between;
}

.logo {
  max-width: 100px;
  padding-top: 17px;
}

.logo img {
  width: 100%;
}

#nav {
  display: flex;
  justify-content: flex-end;
  align-items: center;
}

#nav>li {
  margin: 50px;
}

#nav>li>a {
  color: white;
}

.container {
  padding: 100px 0;
}


.titleBox {
  text-align: center;
}

.titleBox>div {
  margin: 160px 0;
}

.titleBox>div>p {
  font-size: 60px;
  font-weight: 400;
}

.titleBox>div>.maskText>.mask {
  font-weight: 700;
  font-size: 70px;
  /* Permalink - use to edit and share this gradient: https://colorzilla.com/gradient-editor/#0060af+2,e93cec+100 */
  background: rgb(0, 96, 175);
  /* Old browsers */
  background: -moz-linear-gradient(-45deg, rgba(0, 96, 175, 1) 2%, rgba(233, 60, 236, 1) 100%);
  /* FF3.6-15 */
  background: -webkit-linear-gradient(-45deg, rgba(0, 96, 175, 1) 2%, rgba(233, 60, 236, 1) 100%);
  /* Chrome10-25,Safari5.1-6 */
  background: linear-gradient(135deg, rgba(0, 96, 175, 1) 2%, rgba(233, 60, 236, 1) 100%);
  /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
  filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#0060af', endColorstr='#e93cec', GradientType=1);
  /* IE6-9 fallback on horizontal gradient */

  background-clip: text;
  color: rgba(0, 0, 0, 0);
}
.titleBox h2{
  font-family: Lobster;
  animation: neon2 1.5s ease-in-out infinite alternate;
}
@keyframes neon2 {
  from {
    text-shadow: 0 0 10px #fff, 0 0 20px #fff, 0 0 30px #fff, 0 0 40px #228DFF, 0 0 70px #228DFF, 0 0 80px #228DFF, 0 0 100px #228DFF, 0 0 150px #228DFF;
  }
  to {
    text-shadow: 0 0 5px #fff, 0 0 10px #fff, 0 0 15px #fff, 0 0 20px #228DFF, 0 0 35px #228DFF, 0 0 40px #228DFF, 0 0 50px #228DFF, 0 0 75px #228DFF;
  }
}
.titleBox p:last-child{
  font-family: Comfortaa;
  font-weight: 700;
  animation: neon4 1.5s ease-in-out infinite alternate;
}
@keyframes neon4 {
  from {
    text-shadow: 0 0 10px #fff, 0 0 20px #fff, 0 0 30px #fff, 0 0 40px #B6FF00, 0 0 70px #B6FF00, 0 0 80px #B6FF00, 0 0 100px #B6FF00, 0 0 150px #B6FF00;
  }
  to {
    text-shadow: 0 0 5px #fff, 0 0 10px #fff, 0 0 15px #fff, 0 0 20px #B6FF00, 0 0 35px #B6FF00, 0 0 40px #B6FF00, 0 0 50px #B6FF00, 0 0 75px #B6FF00;
  }
}

