.innerBox{
    position: relative;
}
.aboutWrap{
    /* display: flex; */
    flex-wrap: wrap;
    justify-content: space-between;
    text-align: left;
}
.box{
    background: white;
    padding: 25px;
    border-radius: 20px;
    display: flex;
    justify-content: space-evenly;
    width: 32%;
    margin: 20px 0;
    box-sizing: border-box;
}
h2{
    font-size: 34px;
}
p{
    margin-top: 2.5%;
}
.aboutWrap>.box:nth-child(2)>.imgBox,
.aboutWrap>.box:nth-child(5)>.imgBox{
    width:60px;
}
.box .imgBox{
    width:50px;
    padding: 0 20px;
}
.box .imgBox img{
    width: 100%;
}
.box .textBox{
    flex:1;
}
.box .textBox strong{
    color: #000;
}
.box .textBox p{
    color:#666;
}
.historyWrap{
    margin-top: 2%;
    background-color: white;
    width:35%;
    padding: 2% 3%;
    border-radius: 20PX;
}
.historyWrap>p{
    margin-top: 1%;
    color: #000;
    /* line-height: 2; */
}
.bgWrap{
    position: absolute;
    top:50%;
    transform: translateY(-50%);
    right:0;
    max-width: 700px;
    width:100%;
}
.bgWrap>img{
    width: 100%;
}